import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpModule } from '@angular/http';
import { InterceptorHttpService } from './../providers/InterceptorHttpService';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { LoginPage } from '../pages/login/login';
import { ViewProduct } from '../pages/view-product/view-product';
import { MyCart } from '../pages/my-cart/my-cart';
import { FinalizarPage } from '../pages/finalizar/finalizar';
import { PedidoPage } from '../pages/pedido/pedido';
import { VerPedidoPage } from '../pages/ver-pedido/ver-pedido';
import { RegisterPage } from '../pages/register/register';
import { PerfilPage } from '../pages/perfil/perfil';
import { UsuariosPage } from '../pages/usuarios/usuarios';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { CookieService } from 'angular2-cookie/core';
import { LoginServiceProvider } from '../providers/login-service/login-service';
import { Cart } from '../providers/cart/cart';
import { Geolocation } from '@ionic-native/geolocation';
import { Camera } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    ViewProduct,
    MyCart,
    FinalizarPage,
    PedidoPage,
    VerPedidoPage,
    RegisterPage,
    PerfilPage,
    UsuariosPage
  ],
  imports: [
    HttpClientModule,
    HttpModule,
    BrowserModule,
    IonicModule.forRoot(MyApp),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    LoginPage,
    ViewProduct,
    MyCart,
    FinalizarPage,
    PedidoPage,
    VerPedidoPage,
    RegisterPage,
    PerfilPage,
    UsuariosPage
  ],
  providers: [
    Cart,
    CookieService,
    StatusBar,
    SplashScreen,
    FileTransfer,
    FileTransferObject,
    File,
    Camera,
    {
        provide:HTTP_INTERCEPTORS, useClass: InterceptorHttpService, multi:true
    },
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    LoginServiceProvider,
    Geolocation
  ]
})
export class AppModule {}



