import { Component, OnInit } from '@angular/core';
import { NavController, LoadingController } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { FinalizarPage } from '../finalizar/finalizar';
import { ViewProduct } from '../view-product/view-product';
import { CookieService } from 'angular2-cookie/core';
import { RequestOptions } from '@angular/http';
import { Utils } from '../../entity/Utils';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements OnInit{

  produtos :any = [];
  private getUrl:String;

  constructor(public navCtrl: NavController,
    private cookieService:CookieService,
    private requestOptions: RequestOptions,
    private http:HttpClient,
    public loadingCtrl: LoadingController) {
    this.getUrl = Utils.getUrlBackend();
  }

  curtir(p) {
    this.http.post(Utils.getUrlBackend()+'api/like-produtos', {
      produto_id: p.id
    }).subscribe(data => {
      this.atualizarLike(p.id, data);
    }, err => {
      console.log(err)
    });
  }
  atualizarLike(id, data) {  
    for (let i = 0; i < this.produtos.length; i++) {
      if (this.produtos[i].id == id) {
        this.produtos[i].likes = data.likes;
      }
    }
  }

  abrirProduto(p) {
    this.navCtrl.push(ViewProduct,{product:p});
  }

  finalizar() {
  	this.navCtrl.setRoot(FinalizarPage);
  }

  sair() {
  	this.cookieService.removeAll();
    this.requestOptions.headers.set('Authorization', "Bearer ");
    console.log('saiu...')
    this.navCtrl.setRoot(LoginPage);
  }

  ngOnInit() {
    let loading = this.loadingCtrl.create({
      content: 'Carregando...'
    });

    loading.present();
    this.http.get(Utils.getUrlBackend()+'api/products')
    .subscribe(data => {
      this.produtos = data;
      loading.dismiss();
    }, err => {
      console.log(err);
      loading.dismiss();
      
    });
  }

}



