import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import {
  FormBuilder,
  Validators
} from '@angular/forms';	
import {LoginServiceProvider } from '../../providers/login-service/login-service';
import { CookieService } from 'angular2-cookie/core';
import { RequestOptions } from '@angular/http';
import {HomePage} from '../home/home';
import {RegisterPage} from '../register/register';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
	public loginForm;
  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder,
  	private loginService:LoginServiceProvider,
  	private cookieService:CookieService,
    private requestOptions: RequestOptions,
    public alertCtrl: AlertController,
    private toastCtrl: ToastController
    ) {
  	this.loginForm = formBuilder.group({
      email: ['', Validators.required],
      senha: ['', Validators.required]
    });
  }

  cadastrar()
  {
    this.navCtrl.push(RegisterPage);
  }

  showAlert(msg) {
    let toast = this.toastCtrl.create({
      message: msg,
      duration: 3000,
      position: 'top'
    });

    toast.present();
  }

  login() {
  	this.loginService.login(this.loginForm.value).subscribe(
      res => {
        this.loginSuccess(res);
      },
      err => {
        console.log(err)
        if(err.status === 401) {
          this.showAlert('UsuÃ¡rio ou senha incorreto!');
        }else {
          this.showAlert('Erro de conexÃ£o!');
        }
      }
      );

  }

  public loginSuccess(res: any) {
    this.cookieService.removeAll();
    this.cookieService.put("accessToken", res.access_token);
    this.cookieService.put("refreshToken", res.refresh_token);
    
    this.loginService.getUsuarioAtual(res.access_token)
    .subscribe(res => this.redirectPage(res));

  }
  public redirectPage(res: any) {
    this.cookieService.putObject("usuarioAtual", res);
    this.navCtrl.setRoot(HomePage);
  }

  redirectUser(response) {
    this.cookieService.removeAll();
    this.cookieService.put("accessToken", response.access_token);
  }

  ionViewDidLoad() {
    if (this.cookieService.getObject("usuarioAtual")) {
      this.requestOptions.headers.set('Authorization', "Bearer " + this.cookieService.get("accessToken"));
      this.navCtrl.setRoot(HomePage);
    } 
  }

}



