import { Component } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { IonicPage, NavController, NavParams, AlertController, LoadingController, ToastController } from 'ionic-angular';
import { Http } from '@angular/http';
import { Utils } from '../../entity/Utils';
import { LoginPage } from '../login/login';
@IonicPage()
@Component({
	selector: 'page-register',
	templateUrl: 'register.html',
})

export class RegisterPage {

	private url;
	private formUser: FormGroup;

	constructor(public navCtrl: NavController, 
		public navParams: NavParams, 
		private formBuilder: FormBuilder,
		public http: Http,
		private alertCtrl: AlertController,
  	public loadingCtrl: LoadingController,
  	private toastCtrl: ToastController
		) {
		this.url = Utils.getUrlBackend() + 'api/users';

		this.formUser = this.formBuilder.group({
			name: ['', Validators.required],
			telefone: ['', Validators.required],
			email: ['', Validators.required],
			password: ['', Validators.required]
		});
	}



	mostrarAlerta(dados) {
		this.alertCtrl.create({
			title : dados[0],
			subTitle: dados[1],
			buttons: ['OK']
		}).present();
	}

	cadastrar() {
		let loading = this.loadingCtrl.create({
      content: 'Enviando...'
    });

    loading.present();

		let dados:any = this.formUser.value;
		console.log('dados',dados);

		this.http.post(this.url, dados)
		.subscribe(data => {
			data = data.json();
			loading.dismiss();
			console.log(data, 'cadastrado');
			this.alertCtrl.create({
				title: 'Alerta',
				subTitle: 'Cadastro realizado com sucesso!',
				buttons: [ 
				{
	        text: 'Ok',
	        handler: () => {
						this.navCtrl.setRoot(LoginPage);
	        }
      	}
				]
			}).present();
		}, err => {
			if(err.status === 400) {
				err = err.json();
				console.log('400',err);
				for (var chave in err) {
					this.presentToast(err[chave]);
					loading.dismiss();
					break;
				}
				
			} else {
				console.log('erro', err.json())
				loading.dismiss();
				this.presentToast('Erro ao cadastrar');
			}
		});
	}

	presentToast(message) {
	  let toast = this.toastCtrl.create({
	    message: message,
	    duration: 5000,
	    position: 'botton'
	  });
	  toast.present();
	}


}



