import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { Validators } from '@angular/forms';
import { Slides } from 'ionic-angular';
import { ToastController } from 'ionic-angular';
import { Cart } from '../../providers/cart/cart';
import { MyCart } from '../my-cart/my-cart';
import { Utils } from '../../entity/Utils';


@Component({
  selector: 'page-view-product',
  templateUrl: 'view-product.html',
  providers : [Validators]
})
export class ViewProduct {

  getUrl:String;

	@ViewChild(Slides) slides: Slides;
  private product:any;
	
  constructor(public navCtrl: NavController, 
    public navParams: NavParams, 
  	private cart: Cart, 
    private toastCtrl: ToastController
    ) {
    this.getUrl = Utils.getUrlBackend();
  	this.product = this.navParams.get('product');
  }

  addItem() {
    if (!(Number(this.product.quantidade) > 0)) {
      this.toastCtrl.create({
          message: 'Insira uma quantidade vÃ¡lida',
          duration: 3000,
          position: 'bottom'
        })
      .present();
    } else {
      /*this.product["quantidade"] = Number(this.cadastro.value.amount);
      this.product["descricao"] = this.cadastro.value.descricao;*/
      this.cart.addItem(this.product);
      this.navCtrl.push(MyCart);
    }
  	
  }

}



