import { Component, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import { Http } from '@angular/http';
import { HttpClient } from '@angular/common/http';
import { IonicPage, NavController, NavParams, Platform, AlertController, LoadingController } from 'ionic-angular';
import { Cart } from '../../providers/cart/cart';
import { Utils } from '../../entity/Utils';
import { Geolocation } from '@ionic-native/geolocation';
import { HomePage } from '../home/home';
import { LoginPage } from '../login/login';
declare const google:any;
@IonicPage()
@Component({
  selector: 'page-finalizar',
  templateUrl: 'finalizar.html',
})
export class FinalizarPage {

	@ViewChild('map') mapElement: ElementRef;
 	map: any;
 	marker:any;

	private pedido:any = {};
	posicao:String;

  constructor(public navCtrl: NavController, 
  	public navParams: NavParams, 
  	private cart: Cart,
  	private geo: Geolocation,
  	private platform: Platform,
  	private ref:ChangeDetectorRef,
  	private alertCtrl: AlertController,
  	private http:HttpClient,
  	public loadingCtrl: LoadingController
  	) {
  	this.inicializaCampos();
  }
  inicializaCampos() {
  	this.pedido.endereco = '';
  }

  finalizarPedido() {
  	this.pedido.itens = this.cart.itens;
  	this.presentConfirm();

  }
  get isValid() {
  	return this.pedido.endereco != '';
  }
  showMapa(position) {

  	// Map Options
  	let options = {
  		center: position,
  		zoom: 15
  	}

  	this.map = new google.maps.Map(this.mapElement.nativeElement, options);
  	this.marker = new google.maps.Marker({
          position: position,
          map: this.map,
          draggable: true,
          title: 'Meu EndereÃ§o'
        });

  	google.maps.event.addListener(this.marker, 'dragend',  () => {
  		let latitude 	= this.marker.position.lat(); 
  		let longitude = this.marker.position.lng();

    	this.setLatLng(latitude, longitude);
    	this.ref.detectChanges();
		});

  }

  ionViewDidLoad() {
  	this.platform.ready().then(() => {
  		this.geo.getCurrentPosition().then(pos => {
  			this.setLatLng(pos.coords.latitude, pos.coords.longitude);
  			let position = { lat: pos.coords.latitude, lng: pos.coords.longitude }
  			this.showMapa(position);
  			this.posicao = pos.coords.latitude + ' - ' + pos.coords.longitude;
  		}).catch(err => {

  		});
  	});
	}

	setLatLng(lat, lng) {
		this.pedido.latitude = lat;
		this.pedido.longitude = lng;
	}

	presentConfirm() {
  let alert = this.alertCtrl.create({
    title: 'Confirmar Pedido',
    message: 'Deseja enviar este pedido?',
    buttons: [
      {
        text: 'NÃ£o',
        role: 'cancel',
        handler: () => {
        }
      },
      {
        text: 'Enviar',
        handler: () => {
        	this.enviarDados();
        }
      }
    ]
  });
  alert.present();
	}

	private enviarDados() {
		let loading = this.loadingCtrl.create({
      content: 'Enviando...'
    });

    loading.present();

  	this.http.post(Utils.getUrlBackend()+'api/pedidos', this.pedido)
    .subscribe(data => {
      loading.dismiss();
      this.limparCesta();
      this.navCtrl.setRoot(HomePage);
    }, err => {
      loading.dismiss();
      if (err.status == 401) {
      	let erro:any = JSON.parse(err._body);
      	if(erro.error == 'Unauthenticated.') {
      		this.navCtrl.setRoot(LoginPage);
      	}
      }
    })
	}

	private limparCesta() {
		this.cart.itens = [];
		this.pedido.endereco = '';
		this.pedido.numero = '';
		this.pedido.complemento = '';
		this.pedido.forma_pagamento = '';
		this.pedido.troco = '';
		this.pedido.latitude = '';
		this.pedido.longitude = '';
	}

}



