import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { RequestOptions } from '@angular/http';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { PedidoPage } from '../pages/pedido/pedido';
import { CookieService } from 'angular2-cookie/core';
import { PerfilPage } from '../pages/perfil/perfil';
import { UsuariosPage } from '../pages/usuarios/usuarios';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = HomePage;
  usuario:any = {name: '', telefone: ''};

  pages: Array<{title: string, component: any, icone: String}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen,
    private cookieService:CookieService,
    private requestOptions:RequestOptions,
    ) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'InÃ­cio', component: HomePage, icone: 'home' },
      { title: 'Meu Perfil', component: PerfilPage, icone: 'contact' },
      { title: 'Meus Pedidos', component: PedidoPage, icone: 'pricetags' },
      { title: 'Clientes', component: UsuariosPage, icone: 'contact' }
    ];

  }

  logout() {
    this.cookieService.removeAll();
    this.requestOptions.headers.set('Authorization', "Bearer ");
    console.log('saiu..')
    this.nav.setRoot(LoginPage);
  }

  initializeApp() {
    this.platform.ready().then(() => {
      if (this.cookieService.getObject("usuarioAtual")) {
        this.usuario = this.cookieService.getObject("usuarioAtual");
        this.requestOptions.headers.set('Authorization', "Bearer " + this.cookieService.get("accessToken"));
        this.nav.setRoot(HomePage);
      } else {
        this.nav.setRoot(LoginPage);
      }
      // Push Notification
      if (this.platform.is('cordova')) {

        let funcaoRetorno = (data) => {
           console.log('NotificaÃ§Ãµes: ' + JSON.stringify(data));
        };

        window["plugins"].OneSignal.startInit("d23146a3-aacd-4e97-b66e-0cbe7eab947d",
            "331319072068")
            .handleNotificationOpened(funcaoRetorno)
            .endInit();
      }
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



