import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController, ActionSheetController } from 'ionic-angular';
import { CookieService } from 'angular2-cookie/core';
import { Utils } from '../../entity/Utils';
import { Http } from '@angular/http';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File, FileEntry} from "@ionic-native/file";
import { Camera, CameraOptions } from '@ionic-native/camera';

@IonicPage()
@Component({
	selector: 'page-perfil',
	templateUrl: 'perfil.html',
})
export class PerfilPage {
	private user:any;
	private url;
	private urlImage;

	private load;


	constructor(public navCtrl: NavController, 
		public navParams: NavParams, 
		private cookieService:CookieService,
		private toastCtrl: ToastController, 
		private loadingCtrl:LoadingController, 
		private http:Http,
		private transfer: FileTransfer,
		private camera: Camera,
		private readonly file: File,
		private actionSheetCtrl: ActionSheetController) {
		this.user = cookieService.getObject("usuarioAtual");
		this.url = Utils.getUrlBackend() + 'api/users/update';
		this.urlImage = Utils.getUrlBackend() + 'images/fotos/';

		this.load = loadingCtrl.create({
			content: 'Carregando...'
		});
	}

	alterar() {
		let loading = this.loadingCtrl.create({
			content: 'Enviando...'
		});

		loading.present();

		this.http.post(this.url, this.user)
		.subscribe(data => {
			console.log(data);
			data = data.json();
			this.cookieService.putObject("usuarioAtual", data);
			this.user = data;
			loading.dismiss();
			this.presentToast('Alterado com sucesso!');
			console.log(data, 'cadastrado');
			
		}, err => {
			if(err.status === 500)
				console.log(err._body);
			if(err.status === 400) {
				err = err.json();
				console.log('400',err);
				for (var chave in err) {
					this.presentToast(err[chave]);
					loading.dismiss();
					break;
				}
				
			} else {
				loading.dismiss();
				this.presentToast('Erro ao cadastrar');
			}
		});
	}

	presentToast(message) {
		let toast = this.toastCtrl.create({
			message: message,
			duration: 5000,
			position: 'botton'
		});
		toast.present();
	}

	presentActionSheet() {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'O que deseja fazer?',
      buttons: [
        {
          text: 'Galeria',
          handler: () => {
            this.onTakePicture(this.camera.PictureSourceType.PHOTOLIBRARY);
          }
        },{
          text: 'CÃ¢mera',
          handler: () => {
            this.onTakePicture(this.camera.PictureSourceType.CAMERA);
          }
        },{
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

	onTakePicture(sourceType) {
		this.load.present();
		this.camera.getPicture({
      sourceType: sourceType,
      destinationType: this.camera.DestinationType.FILE_URI,
      quality: 100,
      encodingType: this.camera.EncodingType.JPEG,
    }).then(imageData => {
      //this.image = imageData;
      this.uploadPhoto(imageData);
    }, error => {
    	console.log(error);
    });
  }

  displayErrorAlert(err){
    console.log(err);
    this.presentToast('erro ao capturar foto');
  }

  private uploadPhoto(imageFileUri: any): void {

    this.file.resolveLocalFilesystemUrl(imageFileUri)
      .then(entry => (<FileEntry>entry).file(file => this.readFile(file)))
      .catch(err => console.log('upload foto',err));
  }

  private readFile(file: any) {
    const reader = new FileReader();
    reader.onloadend = () => {
      const formData = new FormData();
      const imgBlob = new Blob([reader.result], {type: file.type});
      let dados = {photo: imgBlob};
      formData.append('file', imgBlob, file.name);
      this.postData(formData);
    };
    reader.readAsArrayBuffer(file);
  }
  private postData(formData: FormData) {
    this.http.post(Utils.getUrlBackend()+'api/upload', formData)
      .subscribe(result => {
      	let dados = result.json();
      	console.log(dados);
      	this.user.foto = dados.foto;
      	this.cookieService.putObject('usuarioAtual', dados);
      	this.load.dismiss();
      },
      err => {
      	this.load.dismiss();
      	console.log(err._body);
      });
  }
}



