import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Cart } from '../../providers/cart/cart';
import { ViewProduct } from '../view-product/view-product';
import { HomePage } from '../home/home';
import { FinalizarPage } from '../finalizar/finalizar';
import { Utils } from '../../entity/Utils';

@Component({
  selector: 'page-my-cart',
  templateUrl: 'my-cart.html',
})
export class MyCart {

  getUrl:String;

  constructor(public navCtrl: NavController, public navParams: NavParams,
   private cart: Cart
   ) {
    this.getUrl = Utils.getUrlBackend();
  }
  get itens() {
  	return this.cart.itens;
  }

  buy(){
  	this.navCtrl.push(FinalizarPage);
    
  }
  verItemPedido(p) {
    this.navCtrl.push(ViewProduct, {product: p});
  }
  irParaHome() {
    this.navCtrl.setRoot(HomePage);
  }
}



